// style.scss

.fb-accordion-item-main-wrapper {
    .fb-accordion-item-title-wrapper {
        .fb-accordion-title {
            margin: 0;
        }
    }
}

.fb-accordion-content-wrapper {
    overflow: hidden;
    // max-height: 0;
    transition: max-height 0.3s ease-out;
}

.fb-accordion-content-wrapper.active {
    overflow: visible;
    // max-height: unset;
    transition: max-height 0.3s ease-in;
}